<template>
  <div id="login">
    <div class="logo">
      <img width="200px" src="../assets/img/logo.png" />
      <h1>Swiper</h1>
    </div>

    <div class="container-fluid">
      <fieldset class="form-group">
        <input class="form-control col-10 offset-1" placeholder="请输入手机号" v-model="phonenum" />
      </fieldset>

      <fieldset class="form-group">
        <input class="form-control col-10 offset-1" placeholder="请输入验证码" v-model="vcode" v-show="seen" />
      </fieldset>

      <button class="btn col-10 btn-primary" v-on:click="button_clicked()">{{ btn_text }}</button>
    </div>
  </div>
</template>

<script>
import request from "../libs/http";
import { User } from "../libs/models";

export default {
  name: "Login",
  props: ["self"],

  data: function() {
    return {
      phonenum: "",
      vcode: "",
      seen: false,
      btn_text: "发送验证码",
    };
  },

  methods: {
    // 按钮事件
    button_clicked: function() {
      if (this.seen === false) {
        this.get_vcode();
      } else {
        this.submit_vcode();
      }
    },

    // 获取验证码
    get_vcode: function() {
      request
        .get("/api/user/vcode/fetch", {
          params: { phonenum: this.phonenum },
        })
        .then(function(response) {
          if (response.data.code === 0) {
            alert("消息已发送，请查看手机短信");
          }
        });
      this.seen = true;
      this.btn_text = "登陆";
    },

    // 登陆
    submit_vcode: function() {
      request
        .post("/api/user/vcode/submit", {
          phonenum: this.phonenum,
          vcode: this.vcode,
        })
        .then((response) => {
          if (response.data.code === 0) {
            let cur_user = new User(response.data.data);
            this.$store.commit("set_self", cur_user);
            this.$router.push("/user/show");
          }
        });
    },
  },
};
</script>

