<template>
  <div v-if="friends.length > 0" class="list-group">
    <router-link
      v-for="friend in friends"
      :key="friend.id"
      :to="{ name: 'friend', params: { friend: friend } }"
      class="list-group-item list-group-item-action list-item"
    >
      <img class="icon navbar-brand" :src="friend.avatar" />
      <span class="item-text">{{ friend.nickname }}</span>
      <span class="item-text">@</span>
      <span class="item-text">{{ friend.location }}</span>
    </router-link>
  </div>

  <div v-else class="alert alert-warning notice" role="alert">
    <strong>您暂时没有好友</strong>
    <br />
    <br />
    <span>
      互相喜欢的人，才能成为好朋友！
      <br />
      加油让自己变得更受欢迎！！
    </span>
  </div>
</template>

<script>
import request from "../libs/http";

export default {
  name: "Friends",

  data: function() {
    return {
      friends: [],
    };
  },

  created: function() {
    request.get("/api/social/friends").then((response) => {
      if (response.data.code === 0) {
        this.friends = response.data.data;
      }
    });
  },
};
</script>

