<template>
  <div class="container-fluid upload-box">
    <div class="form-horizontal">
      <h5>上传您的个人形象</h5>
      <hr />

      <div class="form-group">
        <input @change="handle_file($event.target.files)" type="file" accept="image/*" />
      </div>

      <button class="btn btn-primary col-12 btn-txt" v-on:click="upload_avatar()">点击提交</button>
    </div>
  </div>
</template>

<script>
import request from "../libs/http";
import * as qiniu from "qiniu-js";

export default {
  name: "UploadAvatar",

  data() {
    return { file: undefined };
  },

  methods: {
    // Handles a change on the file upload
    handle_file: function(files) {
      this.file = files[0];
      console.log("选中文件:\n", this.file);
    },

    upload_avatar: function() {
      request.get("/qiniu/token").then((response) => {
        if (response.data.code === 0) {
          let key = response.data.data.key;
          let token = response.data.data.token;
          this.upload_to_qn(key, token);
        }
      });
    },

    upload_to_qn: function(key, token) {
      let putExtra = {
        fname: this.file.name, // 原始文件名
        params: {},
        mimeType: ["image/png", "image/jpeg", "image/gif"],
      };

      // 定义上传配置
      let config = {
        useCdnDomain: false, // 启用 CDN
        region: qiniu.region.z0, // 华东区
        retryCount: 1, // 重试次数
      };

      // 定义观察者
      let observer = {
        next(res) {
          console.log(`上传进度: ${res.total.percent}`);
        },
        error(err) {
          console.log(err);
        },
        complete: (res) => {
          console.log("上传成功");
          this.$store.state.self.avatar = res.data;
          this.$router.push("/user/show");
        },
      };

      //开始上传
      let observable = qiniu.upload(this.file, key, token, putExtra, config);
      observable.subscribe(observer); // 监控
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.upload-box {
  padding: 10px 50px;
  text-align: left;
}
</style>
