<template>
  <div id="app">
    <Navbar v-if="show_nav" />
    <router-view :self="self"></router-view>
  </div>
</template>

<script>
import Navbar from "./components/Navbar";

export default {
  name: "App",
  components: { Navbar },

  computed: {
    self: function() {
      return this.$store.state.self;
    },

    show_nav: function() {
      return this.$route.path !== "/" && this.$route.path !== "/login";
    },
  },
};
</script>

