<template>
  <div v-if="queue.length > 0" class="vue-tinder">
    <Tinder ref="tinder" key-name="id" :queue.sync="queue" :offset-y="15" @submit="on_submit">
      <template slot-scope="scope">
        <div
          class="pic"
          @click="show_stanger"
          :style="{
            'background-image': `url(${scope.data.avatar})`,
          }"
        />
      </template>
      <img class="like-pointer" slot="like" src="../assets/img/like-txt.png" />
      <img class="nope-pointer" slot="nope" src="../assets/img/nope-txt.png" />
      <img class="super-pointer" slot="super" src="../assets/img/super-txt.png" />
      <img class="rewind-pointer" slot="rewind" src="../assets/img/rewind-txt.png" />
    </Tinder>

    <div class="swipe-btns">
      <img src="../assets/img/rewind.png" @click="decide('rewind')" />
      <img src="../assets/img/nope.png" @click="decide('nope')" />
      <img src="../assets/img/super-like.png" @click="decide('super')" />
      <img src="../assets/img/like.png" @click="decide('like')" />
    </div>
  </div>

  <div v-else class="alert alert-primary notice" role="alert">
    <strong>亲，您的眼光太高，满足条件的都被您滑完了！</strong>
    <br />
    <br />
    <span>适当调整交友资料可以认识更多有趣的人哦！</span>
  </div>
</template>

<script>
import Tinder from "vue-tinder";
import request from "../libs/http";

export default {
  name: "Swiper",
  components: { Tinder },

  data: function() {
    return {
      queue: this.$store.state.queue,
      offset: 0,
      history: [],
    };
  },

  created: function() {
    if (this.queue.length <= 5) {
      this.fetch_data();
    }
  },

  methods: {
    show_stanger: function() {
      this.$router.push({
        name: "stanger",
        params: { stanger: this.queue[0] },
      });
    },

    fetch_data: function() {
      request.get("/api/social/rcmd").then((response) => {
        if (response.data.code == 0) {
          let uid_list = this.queue.map((u) => u.id);
          response.data.data.forEach((u) => {
            if (uid_list.indexOf(u.id) === -1) {
              this.queue.push(u);
            }
          });
        } else {
          console.log(`获取数据失败: ${response.data.data}`);
        }
      });
    },

    on_submit: function({ type, key, item }) {
      this.swipe(type, key);
      if (this.queue.length < 5) {
        this.fetch_data();
      }
      this.history.push(item);
    },

    async decide(choice) {
      if (choice === "rewind") {
        this.rewind();
      } else if (this.queue.length === 0) {
        alert("手速太快，都被你滑完了！\n\n可以调整交友资料，匹配一些不同的好友！");
      } else {
        this.$refs.tinder.decide(choice);
      }
    },

    swipe: function(stype, sid) {
      let types = { nope: "dislike", like: "like", super: "superlike" };
      console.log(stype, sid);
      let url = `/api/social/${types[stype]}`;
      request.post(url, { sid: sid }).then((response) => {
        if (response.data.code === 0) {
          if (["like", "super"].includes(stype) && response.data.data.is_matched) {
            alert("恭喜，匹配成功！！！");
          }
        } else if (response.data.code === 1006) {
          console.log(`重复滑动: ${sid}`);
        } else {
          this.$refs.tinder.rewind([this.history.pop()]); // 撤销异常
        }
      });
    },

    rewind: function() {
      if (this.history.length) {
        request.post("/api/social/rewind").then((response) => {
          if (response.data.code === 0) {
            this.$refs.tinder.rewind([this.history.pop()]);
          }
        });
      } else {
        alert("您还没有滑过任何人");
      }
    },
  },
};
</script>

