.like-pointer,
.super-pointer,
.nope-pointer,
.rewind-pointer {
  position: absolute;
  z-index: 10;
  width: 112px;
  height: 78px;
  margin: auto;
}
.like-pointer {
  top: 20px;
  left: 10px;
}
.super-pointer {
  bottom: 10px;
  left: 0;
  right: 0;
}
.nope-pointer,
.rewind-pointer {
  top: 20px;
  right: 10px;
}

