<template>
  <div v-if="users.length > 0" class="list-group">
    <router-link
      v-for="user in users"
      :key="user.id"
      :to="{ name: 'stanger', params: { stanger: user } }"
      class="list-group-item list-group-item-action list-item"
    >
      <span class="rank-txt">{{ user.rank }}.</span>
      <img class="icon navbar-brand" :src="user.avatar" />
      <span class="item-txt">{{ user.nickname }} / {{ user.score }} 分</span>
    </router-link>
  </div>

  <div v-else class="alert alert-primary notice" role="alert">
    <strong>暂无排名数据</strong>
  </div>
</template>

<script>
import request from "../libs/http";

export default {
  name: "Friends",

  data: function() {
    return {
      users: [],
    };
  },

  created: function() {
    request.get("/api/social/rank").then((response) => {
      if (response.data.code === 0) {
        this.users = response.data.data;
      }
    });
  },
};
</script>

<style scoped>
.rank-txt {
  display: inline-block;
  font-weight: bold;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1.5rem;
  margin-right: 20px;
  vertical-align: bottom;
}
</style>
